// flex helpers

.flex {
  display: flex;

  > *.full {
    flex: 1;
  }
  &.column {
    flex-direction: column;
  }
  &.justify-center {
    justify-content: center;
  }
  &.align-center {
    align-items: center;
  }
}

.clean-link {
  text-decoration: none;
  color: inherit;
}

.clean-list {
  list-style-type: none;
